.App {
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background: transparent;
  z-index: 10;
}

.header-content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  height: 4.5rem;
  padding: 1.25rem 2rem;
  margin: 0 auto;
}

.header-title {
  display: inline-block;
  font-weight: 700;
  font-size: 1.5rem;
  color: hsla(0, 0%, 100%, .7);
  line-height: 2rem;
  cursor: pointer;
}

.header-navigation-menu {
  float: right;
}

.header-navigation-item {
  display: inline-block;
  padding: 0 1rem;
  font-weight: 700;
  font-size: 1rem;
  color: hsla(0,0%,100%,.7);
  line-height: 2rem;
  cursor: pointer;
}

.header-hamburger-wrapper {
  float: right;
  -webkit-box-sizing: border-box;
  width: 3.5rem;
  height: 3rem;
  padding: .5rem 0;
  margin-top: -.5rem;
  border: 1px solid rgba(0,0,0,.1);
  border-radius: .25rem;
  text-align: center;
  cursor: pointer;
}

.header-has-background-color {
  background-color: #fff;
  box-shadow: 0 1px 0.3rem hsl(0deg 0% 80% / 80%);
}

.header_is_folded {
  max-height: 0;
}

.header-folded-navigation-menus {
  overflow: hidden;
  -webkit-box-sizing: border-box;
  padding-left: 2rem;
  background-color: #fff;
  -webkit-transition: max-height .2s ease;
}

.header-folded-navigation-menu {
  display: table;
  padding: .75rem 0;
  font-weight:700;
  font-size: 1rem;
  color: #6c757d;
  line-height: 1.25rem;
  cursor: pointer;
}

.header-folded-navigation-menu:first-child {
  margin-top: 1rem;
}

.header-folded-navigation-menu:last-child {
  margin-bottom: 1rem;
}

.introduce {
  position: relative;
}

.introduce::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(180deg,rgba(112,93,80,.8) 0, rgba(112, 93, 80, .8) 90%),
   url(https://c.pxhere.com/photos/7b/1a/code_coding_computer_developer_developing_development_macbook_notebook-913320.jpg!d) 50% no-repeat;
   background-size: cover;
   z-index: -1;
}

.introduce-content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  margin: 0 auto;
  padding: calc(4rem + 4.5rem) 2rem 4rem;
  text-align: center;
}

.introduce-title {
  font-family: Black Han Sans, sans-serif;
  font-weight: 400;
  font-size: 4rem;
  color: #fff;
  word-break: keep-all;
}

.introuduce-description {
  margin: 0 auto 2rem;
  font-weight: 400;
  font-size: 1.25rem;
  color: hsla(0, 0%, 100%, .75);
  line-height: 1.5;
}

.content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  padding: 4rem 2rem;
  margin: 0 auto;
}

.section-title {
  display: table;
  position: relative;
  margin: 0 auto 3rem;
}

.section-title-text {
  color: #000000;
  border-bottom-color: #cccccc;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  font-family: sans-serif;
  font-weight: 400;
  font-size: 3rem;
  line-height: 1.5;
}

.about-me-basic-infos {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  -webkit-box-pack: justify;
}

.aboutMe-info-wrapper:first-child {
  margin-bottom: 2rem;
}

.aboutMe-info-wrapper:nth-child(2) {
  margin-bottom: 2rem;
}

.aboutMe-info-wrapper:nth-child(3) {
  margin-bottom: 2rem;
}

.aboutMe-info-wrapper {
  width: 33.3333%;
}

.aboutMe-basic-info {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  max-width: 14rem;
  margin: 0 auto;
  opacity: 0.8;
}

.aboutMe-icon-img-wrapper {
  width: 2rem;
  min-width: 2rem;
  height: 2rem;
  margin-top: .2rem;
  margin-right: 2rem;
}

.aboutMe-icon-img {
  width: 100%;
}

.aboutMe-field-label {
  margin-bottom: .5rem;
  font-weight: 700;
  font-size: 1.25rem;
}

.aboutMe-field-value {
  font-weight: 400;
  font-size: 1rem;
}

.skills {
  background-color: #f9c51d;
}

.skills-content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  padding: 4rem 2rem;
  margin: 0 auto;
  height: 77rem;
}

.skills-title {
  display: table;
  position: relative;
  margin: 0 auto 3rem;
}

.tech-stacks-container {
  display: flex;
  flex-flow: column wrap;
  align-content: space-around;
  height: calc(100% - (7.5rem + 1px));
}

.tech-stacks {
  -webkit-box-sizing: border-box;
  flex-shrink: 0;
  width: 17rem;
  padding: 1.5rem;
  margin: 0 auto 2rem;
  border-radius: 1rem;
  background-color: #fff;
  -webkit-box-shadow: 1rem 1rem 1rem 0 rgb(68 68 68 / 20%);
}

.tech-stacks:nth-child(6) {
  margin-bottom: 0;
}

.skill_title {
  padding-bottom: .5rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #ccc;
  font-weight: 700;
  font-style: 1.5rem;
  color: #f4623a;
}

.skill-img {
  display: block;
  width: 100%;
}

.archiving {
  background-color: #222;

}

.archiving-content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  padding: 4rem 2rem;
  margin: 0 auto;
}

.archiving-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.archiving-card {
  -webkit-box-sizing: border-box;
  width: 26rem;
  padding: 2rem;
  border-radius: 1rem;
  background-color: #f5f5f5;
  cursor: pointer;
}

.archiving-img-wrapper {
  display: block;
  width: 12rem;
  margin-bottom: 1rem;
}

.archiving-img {
  width: 100%;
}

.archiving-url {
  margin-bottom: 1rem;
  color: #258ddb;
}

.archiving-description {
  color: #444;
}

.projects {
  background-color: #1d809f;
}

.projects-content {
  -webkit-box-sizing: border-box;
  width: 100%;
  max-width: 71.25rem;
  padding: 4rem 2rem;
  margin: 0 auto;
}

.projects-project {
  padding: 3rem;
  margin: 0 auto 2rem;
  border-radius: 1rem;
  background-color: #fff;
  -webkit-box-shadow: 1rem 1rem 1rem 0 rgb(68 68 68 / 20%);
}

.projects-title {
  padding-bottom: .5rem;
  font-family: sans-serif;
  font-weight: 400;
  font-size: 2.5rem;
  color: #222;
  text-align: center;
}

.projects-period {
  margin-bottom: 2rem;
  font-weight: 400;
  font-size: 1rem;
  color: #6c757d;
  text-align: center;
  opacity: .8;
}

.projects-info {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  -webkit-box-pack: center;
}

.projects-descriptions {
  width: calc(50% - 3rem);
}

.projects-main-description {
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  font-weight: 400;
  font-size: 1rem;
}

.projects-description {
  margin-bottom: 1rem;
}

.projects-label {
  display: inline-block;
  vertical-align: top;
  width: 8.4rem;
  font-weight: 900;
  font-size: 1rem;
}

.projects-value {
  display: inline-block;
  vertical-align: bottom;
  width: calc(100%-8.4rem);
  font-weight: 400;
  font-size: 1rem;
}

.projects-url {
  color: #258ddb;
  cursor: pointer;
  word-break: break-all;
}

.footer {
  height: 8rem;
  padding-top: 3rem;
  background-color: #222;
  text-align: center;
}

.footer-copyright {
  font-weight: 400;
  font-size: .9rem;
  color: #f5f5f5;
}

@media (max-width: 1200px) {
  .projects-descriptions{
    width: calc(55% - 2rem);
  }

  .tech-stacks {
    width: 19rem;
  }
}

@media (max-width: 992px) {
  .aboutMe-info-wrapper {
    width: 50%;
  }

  .tech-stacks-container {
    height: calc(100% - (7rem + 1px));
  }

  .archiving-card {
    width: 100%;
    max-width: 30rem;
    margin-bottom: 2rem;
  }

  .projects-descriptions{
    width: 100%;
  }

  .tech-stacks:nth-child(3) {
    margin-bottom: 2rem;
  }

  .skills-content {
    height: 115rem;
  }
}

@media (max-width: 768px) {
  .tech-stacks {
    width: 14.5rem;
  }

  .skills-content {
    height: 93rem;
  }
}

@media (max-width:576px) {
  .projects-project {
    padding: 2rem;
  }
  .aboutMe-info-wrapper {
    width: 100%;
  }
  .aboutMe-info-wrapper:nth-child(4) {
    margin-bottom: 2rem;
  }
  .aboutMe-info-wrapper:nth-child(5) {
    margin-bottom: 2rem;
  }
  .tech-stacks-container {
    height: calc(100% - (6.5rem + 1px));
  }
  .archiving-card {
    padding: 1.5rem;
  }
  .projects-descriptions {
    width: 90%;
  }

  .tech-stacks {
    width: 18rem;
  }

  .tech-stacks:nth-child(3) {
    margin-bottom: 2rem;
  }

  .skills-content {
    height: 177rem;
  }
}

